.app__wrapper {
width: 1200px;
max-width: 100%;
margin: 0 auto;
 display: grid;
 grid-template-areas:
"header header header"
"nav content content";

grid-template-rows: 60px 1fr;
grid-template-columns: 2fr 10fr;
grid-gap: 10px;
}

.app__wrapper__content {
    grid-area: content;
  }
  